.container{

    @media(max-width:1024px){
        padding-bottom: 90px;
        padding-top: 0;
        padding-left: 5%;
        padding-right: 5%;
    }
}

.title{
    text-align: left;
    font-size: 22px;
    // padding-bottom: 20px;
    font-weight: 500;
    letter-spacing: .66px;
    color: #0f0e0e;
}

.buttonMoreContainer{
    display: flex;
    justify-content: end;
    padding-top: 30px;
    padding-bottom: 30px;
    @media(max-width:1024px){
        display: none;
    }

}

.buttonMore{
    cursor: pointer;
    font-size: 16px;
    background-color: #fff;
    border-radius: 3px;
    padding: 10px 35px;
    border: 1px solid #707070;

    @media(max-width: 1024px){
        display: none;
    }
}

.buttonMobile{
    display: none;
    @media(max-width: 1024px){
        display: flex;
        justify-content: center;
    }
}
.readMore{
    background-color: #000;
    color: #fff;
    border-radius: 3px;
    padding: 10px 40px;
    font-size: 20px;
    font-weight: 200;
    cursor: pointer;
    margin-top: 50px;
    // margin-bottom: 60px;
}